import "../estilos/CSS-faqs.css";
import { MDBAccordion, MDBAccordionItem, MDBContainer } from "mdb-react-ui-kit";

const Faqs = () =>{
    return (
    
        <MDBContainer className="mt-5" style={{maxWidth: '1000px'}}>
        <MDBAccordion alwaysOpen initialActive={1}>
        <MDBAccordionItem collapseId={1} headerTitle="¿Cuál es el mejor material para elegir un sofá duradero pero cómodo?">
        El material ideal para un sofá duradero y cómodo depende de tus preferencias y necesidades. El cuero genuino es resistente y fácil de limpiar, mientras que las telas de microfibra ofrecen comodidad y son más asequibles. ¡Visítanos para probar diferentes opciones y encontrar el sofá perfecto para ti!
        </MDBAccordionItem>
        <MDBAccordionItem collapseId={2} headerTitle="¿Ofrecen servicio de entrega y montaje?">
        Sí, en "El rincón del mueble" ofrecemos servicio de entrega y montaje. Nuestro equipo profesional se encargará de llevar tus muebles a tu hogar y ensamblarlos de manera eficiente, asegurándonos de que estén listos para su uso.
        </MDBAccordionItem>
        <MDBAccordionItem collapseId={3} headerTitle="¿Cuál es la mejor manera de cuidar y mantener mis muebles de madera?">
        Para mantener tus muebles de madera en óptimas condiciones, evita la exposición directa al sol y la humedad excesiva. Limpia regularmente con un paño suave y utiliza productos de limpieza específicos para madera. Recuerda proteger las superficies de posibles arañazos con posavasos y tapetes.
        </MDBAccordionItem>
        <MDBAccordionItem collapseId={4} headerTitle="¿Tienen opciones de financiamiento o planes de pago?">
        Sí, entendemos que la compra de muebles puede ser una inversión. Ofrecemos opciones de financiamiento y planes de pago flexibles para adaptarnos a tus necesidades. ¡Consulta con nuestro equipo en la tienda para conocer las opciones disponibles!
        </MDBAccordionItem>
        <MDBAccordionItem collapseId={5} headerTitle="¿Puedo devolver o cambiar un mueble si no estoy satisfecho con mi compra?">
        Sí, en "El rincón del mueble" queremos asegurarnos de que estés satisfecho con tu compra. Ofrecemos políticas de devolución y cambio. Si por alguna razón no estás contento con tu mueble, contáctanos dentro del período especificado en nuestra política para discutir las opciones de devolución o cambio. ¡Queremos garantizar tu completa satisfacción!
        </MDBAccordionItem>
        </MDBAccordion><br/><br/><br/>
        </MDBContainer>
        
    )
}
export default Faqs;