import "../estilos/CSS-blog.css";

const Blog = () =>{
    return (
        <div class="cont-blog">
        <h1 class="h1blog">Las noticias mas importantes de la semana</h1>
    
        <div class="cont-blog">
            <div class="contenedor-subtitulo">
                <h2>Innovación en Materiales Sostenibles:   </h2>
            </div>
            <div class="contenedor-noticia">
                <div class="img" id="img1"></div>
                <p>La industria del mobiliario está experimentando una revolución en términos de sostenibilidad. Las empresas líderes están adoptando materiales innovadores, como maderas certificadas, plásticos reciclados y textiles ecológicos para crear muebles respetuosos con el medio ambiente. Este enfoque refleja la creciente conciencia ambiental de los consumidores y la presión para reducir la huella de carbono en la producción de muebles.</p>
            </div>
        </div>
        {/* segunda noticia */}
        <div class="cont-blog">
            <div class="contenedor-subtitulo">
                <h2>Diseño Modular y Personalización:   </h2>
            </div>
            <div class="contenedor-noticia">
                <div class="img"  id="img2"></div>
                <p>La tendencia hacia la personalización en el diseño de muebles está en auge. Las marcas están adoptando enfoques modulares que permiten a los clientes crear configuraciones únicas y adaptar los muebles a sus necesidades específicas. Esta flexibilidad no solo refleja la preferencia por diseños personalizados, sino también una respuesta a la evolución de los espacios de vida modernos y la demanda de soluciones versátiles.</p>
            </div>
        </div>
        {/* tercera noticia */}
        <div class="cont-blog">
            <div class="contenedor-subtitulo">
                <h2>Realidad Aumentada en la Experiencia de Compra:  </h2>
            </div>
            <div class="contenedor-noticia">
                <div class="img"  id="img3"></div>
                <p>La integración de la realidad aumentada (AR) en la industria del mobiliario está transformando la experiencia de compra. Muchas empresas están desarrollando aplicaciones que permiten a los clientes visualizar cómo se verán los muebles en sus hogares antes de realizar una compra. Esta tecnología ayuda a reducir la incertidumbre al comprar en línea y mejora la toma de decisiones.</p>
            </div>
        </div>
        {/* cuarta noticia */}
        <div class="cont-blog">
            <div class="contenedor-subtitulo">
                <h2>Muebles Inteligentes y Conectados:  </h2>
            </div>
            <div class="contenedor-noticia">
                <div class="img"  id="img4"></div>
                <p>Los muebles inteligentes están ganando popularidad, con características como carga inalámbrica, iluminación ajustable, altavoces integrados y sistemas de control inteligente. La convergencia de la tecnología y el diseño está llevando a la creación de muebles que no solo son funcionales y estéticamente agradables, sino también capaces de mejorar la experiencia del usuario a través de la conectividad y la automatización.</p>
            </div>
        </div>
        {/* quinta noticia */}
        <div class="cont-blog">
            <div class="contenedor-subtitulo">
                <h2>Economía Circular en el Diseño de Muebles:  </h2>
            </div>
            <div class="contenedor-noticia">
                <div class="img"  id="img5"></div>
                <p>La economía circular se está convirtiendo en un enfoque fundamental en la producción de muebles. Las marcas están adoptando estrategias para reducir los residuos y fomentar la reutilización y el reciclaje de materiales. Esto se traduce en la creación de muebles duraderos y fácilmente reciclables, así como en programas de devolución y reutilización para minimizar el impacto ambiental de los productos al final de su vida útil.</p>
            </div>
        </div>
        {/* sexta noticia */}
        <div class="cont-blog">
            <div class="contenedor-subtitulo">
                <h2>Diseño de Muebles Impresos en 3D:  </h2>
            </div>
            <div class="contenedor-noticia">
                <div class="img"  id="img6"></div>
                <p>La tecnología de impresión 3D está revolucionando la fabricación de muebles al permitir diseños más complejos y personalizados. Empresas pioneras están utilizando impresoras 3D para crear piezas únicas con formas y estructuras innovadoras. Esta tendencia no solo ofrece nuevas posibilidades creativas, sino que también podría cambiar la forma en que se produce y distribuye el mobiliario.</p>
            </div>
        </div>
        {/* septima noticia */}
        <div class="cont-blog">
            <div class="contenedor-subtitulo">
                <h2>Influencia de la Cultura Pop en el Diseño:  </h2>
            </div>
            <div class="contenedor-noticia">
                <div class="img"  id="img7"></div>
                <p>El diseño de muebles está siendo cada vez más influenciado por la cultura pop, desde referencias a iconos del cine y la música hasta colaboraciones con artistas contemporáneos. Las marcas están buscando formas de incorporar elementos de la cultura popular en sus diseños, creando piezas que no solo son funcionales, sino también expresiones de estilo y personalidad.</p>
            </div>
        </div>
        {/* octava noticia */}
        <div class="cont-blog">
            <div class="contenedor-subtitulo">
                <h2>Muebles Multifuncionales para Espacios Pequeños:  </h2>
            </div>
            <div class="contenedor-noticia">
                <div class="img"  id="img8"></div>
                <p>Con el aumento de la urbanización y la disminución del espacio habitable, los muebles multifuncionales están en alta demanda. Diseñadores y fabricantes están ideando soluciones creativas para maximizar el espacio en hogares pequeños, como sofás que se transforman en camas, mesas extensibles y muebles que ofrecen almacenamiento integrado.</p>
            </div>
        </div>

    </div>
    )
}
export default Blog;